import React, { Component} from 'react'; 
import Input from '../Input/Input';
import TextArea from '../TextArea/TextArea';
import messageBox from '../MessageBox/MessageBox';
import Button from '../Button/Button';
import validations from '../../validations/validations';
import { messageMailer } from '../../mailer/mailer';

class MessageForm extends Component { 
  constructor(props){
    super(props);
    
    this.setValidationOpts(props);
    this.sendMail = this.sendMail.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.state = {buttonText: this.props.lang.form.button,
                  name: '',
                  nameErrors: [],
                  email: '',
                  emailErrors: [],
                  message: '',
                  messageErrors: [],
                  formMessage: ''};
  }

  setValidationOpts(props){
    this.validationOpts = {
      validations: {
        name: ['presence'],
        email: ['presence', 'emailFormat'],
        message: ['presence']
      },
      messages: {
        presence: props.lang.form.validations.presence,
        emailFormat: props.lang.form.validations.email
      }
    }
  }

  componentWillReceiveProps(nextProps){
    this.setValidationOpts(nextProps); 
  }

  handleChange(event){
    this.setState({[event.target.name]: event.target.value});
  }

  sendMail(event){
		event.preventDefault();
    let formValidations = 
      validations.validate(this.state, this.validationOpts);
    
    if(formValidations.haveErrors){
      this.setState(Object.assign(formValidations.formErrors, 
        {formMessage: messageBox('failure', 
                      this.props.lang.form.failureMessage)}));
      return;
    }else{
      this.setState(Object.assign({buttonText: 
        this.props.lang.form.buttonLoading},
        formValidations.formErrors));
    }
    const params = {
      name: this.state.name,
      email: this.state.email,
      message: this.state.message
    };
    messageMailer.send(params, () => {
      this.setState({buttonText: this.props.lang.form.button,
                     formMessage: messageBox('success', 
                     this.props.lang.form.successMessage)});
    }, (err) => {
      this.setState({buttonText: this.props.lang.form.button,
                     formMessage: messageBox('failure', 
                      err)});
    });
  }


  render(){
    return (
      <div id='message'>
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.subtitle}</h3>
        </div>
        <div className='flex-row inline-center g-pad'>
          <form onSubmit={this.sendMail}>
            <h3>{this.props.lang.subtitle}</h3>
            <Input 
              name='name'
              value={this.state.name}
              placeholder={this.props.lang.form.inputName} 
              autoComplete='name'
              errors={this.state.nameErrors}
              type='text'
              onChange={this.handleChange}
              /> 
            <Input 
              name='email'
              value={this.state.email}
              placeholder={this.props.lang.form.inputEmail} 
              autoComplete='email'
              errors={this.state.emailErrors}
              type='text'
              onChange={this.handleChange} 
              />
            <TextArea 
              name='message'
              value={this.state.message}
              errors={this.state.messageErrors}
              placeholder={this.props.lang.form.inputMessage}
              onChange={this.handleChange} 
              rows='4'
              cols='6'
            />
            {this.state.formMessage}
            <Button 
              onAction={this.sendMail}
              styleName='u-fullwidth'
              text={this.state.buttonText}
            />
          </form>
        </div>
      </div>
    );
  }
} 

export default MessageForm;



