import React, { Component} from 'react'; 
import Input from '../Input/Input';
import Button from '../Button/Button';
import validations from '../../validations/validations';
import Footer from '../Footer/Footer';
import './AccessBudget.css';

class AccessBudget extends Component { 
  constructor(props){
    super(props);

    this.setValidationOpts(props);
    this.state = {
      buttonText: this.props.lang.button,
      budgetCode: '',
      budgetCodeErrors: []
    }
    this.handleChange = this.handleChange.bind(this);
    this.accessBudget = this.accessBudget.bind(this);
  }

  setValidationOpts(props){
    this.validationOpts = {
      validations: {
        budgetCode: ['presence'],
      },
      messages: {
        presence: props.lang.validations.presence
      }
    }
  }

  componentWillReceiveProps(nextProps){
    this.setValidationOpts(nextProps); 
  }

  accessBudget(event){
    event.preventDefault();
    this.setState({buttonText: this.props.lang.button,
    budgetCodeErrors: [this.props.lang.budgetNotFound]})
    let formValidations = 
      validations.validate(this.state, this.validationOpts);
    if(formValidations.haveErrors){
      this.setState(formValidations.formErrors);
    }
  }

  handleChange(event){
    this.setState({[event.target.name]: event.target.value});
  }

  render(){
    return (
      <div>
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.subtitle}</h3>
        </div>
        <div className="access-budget-wrapper flex-row inline-center">
          <form onSubmit={this.accessBudget}>
            <h3>{this.props.lang.message}</h3>
            <Input  
              name='budgetCode'
              value={this.state.budgetCode}
              errors={this.state.budgetCodeErrors}
              type='text'
              placeholder={this.props.lang.inputCode} 
              onChange={this.handleChange}
            />
            <Button styleName='u-fullwidth' 
              onAction={this.accessBudget}
              text={this.state.buttonText} />
          </form>
        </div>
        <Footer 
            lang={this.props.footer}/>
      </div>
    );
  }
} 

export default AccessBudget;



