import React, { Component} from 'react'; 
import Input from '../Input/Input';
import TextArea from '../TextArea/TextArea';
import messageBox from '../MessageBox/MessageBox';
import CheckBox from '../CheckBox/CheckBox';
import Select from '../Select/Select';
import Button from '../Button/Button';
import validations from '../../validations/validations';
import { budgetMailer } from '../../mailer/mailer';

class BudgetForm extends Component { 
  constructor(props){
    super(props);
    this.setValidationOpts(props);
    this.sendMail = this.sendMail.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.state = {buttonText: this.props.lang.form.button,
                  name: '',
                  nameErrors: [],
                  email: '',
                  emailErrors: [],
                  idea: '',
                  ideaErrors: [],
                  web: false,
                  app: false,
                  API: false,
                  support: false,
                  other: false,
                  range: '',
                  addnotes: '',
                  formMessage: ''};
  }

  setValidationOpts(props){
    this.validationOpts = {
      validations: {
        name: ['presence'],
        email: ['presence', 'emailFormat'],
        idea: ['presence']
      },
      messages: {
        presence: props.lang.form.validations.presence,
        emailFormat: props.lang.form.validations.email
      }
    }
  }

  componentWillReceiveProps(nextProps){
    this.setValidationOpts(nextProps);
  }

  handleChange(event){
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : 
                                               target.value;
    const name = target.name;
    this.setState({[name]: value});
  }

  sendMail(event){
    event.preventDefault();
    let formValidations = 
      validations.validate(this.state, this.validationOpts);
    
    if(formValidations.haveErrors){
      this.setState(Object.assign(formValidations.formErrors, 
        {formMessage: messageBox('failure', 
                      this.props.lang.form.failureMessage)}));
      return;
    }else{
      this.setState(Object.assign({buttonText: 
        this.props.lang.form.buttonLoading},
        formValidations.formErrors));
    }
    const allowed = ['name', 'email', 'idea', 'web', 'app', 'API', 
                     'support', 'other', 'range', 'addnotes'];
    const params = Object.keys(this.state).reduce((acc, key) => {
      if(allowed.includes(key))
        acc[key] = this.state[key];
      return acc;
    }, {});
    budgetMailer.send(params, () => {
      this.setState({buttonText: this.props.lang.form.button,
                     formMessage: messageBox('success', 
                     this.props.lang.form.successMessage)});
    }, (err) => {
      this.setState({buttonText: this.props.lang.form.button,
                     formMessage: messageBox('failure', 
                      err)});
    });
  }

  render(){
    const checkboxText = this.props.lang.form.checkbox,
        checkboxes = [
          { name: 'web', state: this.state.web, 
            text: checkboxText.web },
          { name: 'app', state: this.state.app, 
            text: checkboxText.app },
          { name: 'API', state: this.state.API, text: 'API' },
          { name: 'support', state: this.state.support, 
            text: checkboxText.support },
          { name: 'other', state: this.state.other, 
            text: checkboxText.other }
        ];
    return (
      <form onSubmit={this.sendMail}>
        <h3>{this.props.lang.form.title}</h3>
        <Input 
          name='name' 
          value={this.state.name}
          autoComplete="name"
          errors={this.state.nameErrors}
          type='text'
          placeholder={this.props.lang.form.inputName} 
          onChange={this.handleChange}
        />
        <Input  
          name='email'
          value={this.state.email}
          errors={this.state.emailErrors}
          autoComplete="email"
          type='text'
          placeholder={this.props.lang.form.inputEmail} 
          onChange={this.handleChange}
        />
        <h3>{this.props.lang.form.projectType}</h3>
        {checkboxes.map((checkbox, i) => {
          return (
            <CheckBox 
              key={i}
              name={checkbox.name}
              checked={checkbox.state}
              text={checkbox.text}
              onChange={this.handleChange}
              />
          );
        })}
        <TextArea 
          name='idea'
          value={this.state.idea}
          errors={this.state.ideaErrors}
          placeholder={this.props.lang.form.inputIdea} 
          onChange={this.handleChange}
          rows='4'
          cols='6'
        />
        <Select 
          name='range'
          value={this.state.range}
          options={this.props.lang.form.select}
          onChange={this.handleChange}
        />
        <TextArea 
          name='addnotes'
          value={this.state.addnotes}
          placeholder={this.props.lang.form.inputNote}
          onChange={this.handleChange}
          rows='4'
          cols='6'
        />
        {this.state.formMessage}
        <Button styleName='u-fullwidth' 
          onAction={this.sendMail}
          text={this.state.buttonText} />
      </form>
    );
  }
} 



export default BudgetForm;



