import React, { Component} from 'react'; 
import './Footer.css';
import { Link } from 'react-router-dom';

class Footer extends Component { 
  render(){
    return (
      <div className="footer flex-row">
        <div className='footer-wrapper'>
          <div className="g-pad">
            <h3>MARCELL MONTEIRO CRUZ</h3>
            <p>{this.props.lang.email}</p>
            <p>Software Developer</p>
          </div>
          <div className='footer-navigation flex-row wrap g-pad'>
            <div className="footer-navigation-item">
              <h3>{this.props.lang.menu}</h3> 
              <ul>
                <li>
                  <Link to="/">
                    {this.props.lang.home}
                  </Link>
                </li>
                <li>
                  <Link to="/budget">
                    {this.props.lang.budget}
                  </Link>
                </li>
              </ul>
            </div>
            <div className="footer-navigation-item">
              <h3>{this.props.lang.contact}</h3> 
              <ul>
                <li>{this.props.lang.email}</li>
                <li><a>@mmc</a></li>
              </ul>
            </div>
            <div className="footer-navigation-item">
              <h3>SOCIAL</h3>
              <ul>
                <li><a target="_blank" 
                rel="noopener noreferrer"
                href="https://github.com/0000marcell">github</a></li>
              </ul>
            </div>
          </div>
          <div className="g-pad">
            <p>{this.props.lang.copyright}  <a>@mmc</a></p>
          </div>
        </div>
      </div>
    );
  }
} 

export default Footer;



