import React, { Component} from 'react'; 
import Footer from '../Footer/Footer';
import ImageGallery from 'react-image-gallery';
import './ProjectPage.css';
import { NavLink } from 'react-router-dom';

class ProjectPage extends Component { 
  render() {
    return (
      <div>
        <div className="project-page flex-column inline-center g-pad">
          <div className="project-page-title flex-row">
            <img alt="" src={this.props.data.image} />
            <h3>{this.props.data.name}</h3>
          </div>
          <p>./screenshots</p>
          <ImageGallery items={this.props.data.images} />
          <div className="flex-row project-page-links inline-end">
            <div>
              {this.props.data.links.map((item, i) => {
                  if(i === 0){
                    return (<NavLink exact to={item.href} key={i}>
                              {item.name}
                            </NavLink>);
                  }
                  return (
                    <a href={item.href} key={i}>{item.name}</a>
                  )
                })
                }
            </div>
          </div>
          <div className="g-pad">
            <p>{this.props.data.longDescription}</p>
            <p></p>
            <ul>
              {
                this.props.data.technologies.map((tecName, i) => {
                  return (<li key={i}>{tecName}</li>);
                })
              }
            </ul>
          </div>
        </div>
        <Footer 
            lang={this.props.footer}/>
      </div>
    );
  }
} 



export default ProjectPage;



