import React from 'react';

export default (
  <div>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./how is it calculated</h3>
    </div>
    <p>
      In mid 2014 it grew on me the interest of knowing exactly where my time is spent, of this interest came the idea of making an <a target="_blank" rel="noopener noreferrer" href="https://0000marcell.github.io/p36-hours/#/clock">app</a> in order to collect this data, in the beggining the objective was personal, I wanted to know where i was spending my time in order to focus on the subjects that i've judge more important for my work, after some years using this system, came the idea of also using it to be more transparent with my habilities, its good for me and good for the people that hire me, the transparency also motivates me to invest more time on the areas important to my work, the system was used mainly for my work in these last years, but recently i've decided to expand to other areas, data in the app is organized by subtasks and tags which makes it easy to extract it and generate the graphs, if you visit the app you gonna have a pretty good idea of how this is done, you can click on the button "?" to have access to a tour explaning the app</p> 
  <p>it's important to mention that the time spent is specifically writting code or studying and not on a project that involve these things, the time spent in a project involves a lot of things, and writting code is just a part of it, another important thing to mention is that there's overlap between the data, part of the time spent in javascript for example it's also counted in react, since react is made in javascript and you write javascript to write react, it depends on the subtask and the tags</p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./week points of the system</h3>
    </div>
    <p>
      The system doesn't mesure quality just quantity, quality measurements are very hard anyway, the amount of hours spent in some task not necessary converts back to useful learning experience, but its a good measurement, and it gives me some compass to focus on things that are lacking, i don't think its useful to make comparisons between people, but in general is valuable data to have.   
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./About group skill distribution</h3>
    </div>  
    <p>
      I've had the idea of deviding the skills in groups recently when i expanded the use of the app for other things besides development and i hope to focus on measuring these other types of skills in the future. 
    </p>
  </div>
);



