import React, { Component} from 'react'; 
import './MessageBox.css';

class MessageBox extends Component { 
  render(){
    return (
      <div className={`message-box message-${this.props.condition}`}>
        <p>{this.props.message}</p>
      </div>
    );
  }
} 


const messageBox = function(condition, message){
  return <MessageBox 
      message={message}
      condition={condition} />;
}

export default messageBox;



