import React, { Component} from 'react'; 
import './Project.css';
import { NavLink } from 'react-router-dom';

class Project extends Component { 
  render(){
    let projectPageLink = this.props.data.projectPageLink;
    return (
      <div className="project flex-row">
        <div className="project-image">
          <img alt="" src={this.props.data.image} />
        </div>
        <div className="project-body">
          <div className="flex-row inline-space-between wrap">
            <div className="project-title">
              <NavLink exact to={projectPageLink.href}>
                {this.props.data.name}
              </NavLink>
            </div>
            <div className="project-links">
              {this.props.data.links.map((item, i) => {
                if(i === 0){
                  return (<NavLink exact to={item.href} key={i}>
                            {item.name}
                          </NavLink>);
                }
                return (
                  <a href={item.href} key={i}>{item.name}</a>
                )
              })
              }
            </div>
          </div>
          <div>
            <p>{this.props.data.description}</p>
            <NavLink exact to={projectPageLink.href}>
              {projectPageLink.name}
            </NavLink>
          </div>
          <div className="project-tags flex-row wrap inline-end">
            {this.props.data.technologies.map((item, i) => {
              return (
                <div className='tag' key={i}>
                  {item}
                </div>
              );
            })
            }
          </div>
        </div>
      </div>
    );
  }
} 

export default Project;



