const emailRegex = new RegExp('[^@]+@[^@]+\\.[^@]+');

const presence = (val) => {
  return (val) ? true : false; 
}

const emailFormat = (val) => {
  return (emailRegex.test(val)) ? true : false;
}

const tests = {
  presence: presence,
  emailFormat: emailFormat
}

export default {
  validate(state, opts){
    let validations = opts.validations,
        messages = opts.messages,
        results = { haveErrors: false,
                    formErrors: {}};
    Object.keys(validations).forEach((field) => {
      results.formErrors[`${field}Errors`] = [];
      validations[field].forEach((validation) => {
        if(!tests[validation](state[field])){
          results.haveErrors = true;
          results.formErrors[`${field}Errors`]
            .push(`${messages[validation]}`);
        }
      });
    });
    return results;
  }
}



