import React from 'react';

export default (
  <div>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./como  feito o calculo</h3>
    </div>
    <p>
      No meiado de 2014 comecei a ficar interressado em saber exatamente aonde meu tempo  gasto, desse interesse surgiu a idea de criar uma <a target="_blank" rel="noopener noreferrer" href="https://0000marcell.github.io/p36-hours/#/clock">app</a> para coletar esses dados, o objetivo no inicio era totalmente pessoal, eu queria saber aonde eu estava gastando meu tempo pra poder focar nas coisas que eu julgasse mais importantes, depois de alguns anos usando esse sistema surgiu a idea de tambm usa-lo para ser mais transparente nas minhas habilidades, e isso serve tanto para quem me contrata quanto para mim, a transparencia desse sistema tambm me motiva a investir mais tempo em areas importantes para meu trabalho, o sistema foi usado mais para meu trabalho nesses anos, mas recentemente resolvi expandir para outras areas, os dados no app so organizados por subtarefas e tags o que torna bem facil extrai-los e gerar visualizaes, visitando a app da pra ter uma boa idea de como isso  feito, clicando no boto "?" voc tem acesso a uma tour que explica como a app funciona.</p> 
  <p> importante mencionar que o tempo gasto  especificamente escrevendo cdigo ou estudando e no o tempo gasto em um projeto que envolve determinada tecnologia, o tempo gasto em um projeto envolve varias outras coisas, escrever cdigo e s uma parte, outra coisa importante e que existe overlap entre os dados, parte do tempo gasto em javascript por exemplo tambm conta para react, dependendo da tag que a tarefa tem e das subtarefas.</p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./pontos fracos do sistema</h3>
    </div>
    <p>
      Um dos pontos fracos do sistema  no avaliar qualidade, avaliar qualidade  muito dificil de qualquer maneira, e a quantidade de horas em determinada tarefa no necessariamente significa fazendo algo significativo, que ir avanar minha capacidade na determinada habilidade, porm comparativamente, serve para saber a distribuio do meu tempo em cada habilidade pra me guiar em direo a coisas que fazem mais sentido pra mim, no geral o tempo tmbem  um indicador razovel da capacidade em cada coisa, no serve como comparativo entre pessoas distintas, mas como informao pessoal e de forma generalista  valioso.
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./sobre a distribuio das habilidades em grupos</h3>
    </div>  
    <p>
      Tive a idia de distribuir as habilidades em grupos diferentes refentemente, por isso a maior parte do tempo est em desenvolvimento, em parte por causa disso e em parte por que essa  realmente a ara onde gasto a maior parte do meu tempo, predento gastar mais tempo desenvolvendos outros grupos de habilidades, por isso reoganizei meu calendario e fiz algumas projees, dividir as habilidades em grupos me mostra onde gastar meu tempo. 
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./sobre as projees</h3>
    </div>
    <p>
      As projees so feitas inteiramente no calendrio do proximo ano, esse calendrio pode mudar mas eu pretendo pelo menos 
      chagar perto dos resultados da projeo.
    </p>
    <div className="flex-row inline-space-between subtitle g-pad">
      <h3>./Idias e sugestes</h3>
    </div>
    <p>
      Idias e sugestes so sempre bem vindas, eu pretendo mudar o sistema de hras para alguma coisa mais significativa no futuro, e incluir uma parte especifica para desfios, projetos feitos especificamente para melhorar alguma habilidade.
    </p>
  </div>
);



