import config from './config';

const emailjs = window.emailjs;

function send(templateId, params, successCb, failureCb){
  emailjs.init(config.userId);
  emailjs.send(config.serviceId, templateId, params)
    .then(() => { 
    successCb();
  }, (err) => {
    failureCb(err);
  });
}


const messageMailer = {
  send(){
    send(config.messageTemplate, ...arguments);
  }
}

const budgetMailer = {
  send(){
    send(config.budgetTemplate, ...arguments);
  }
}

export { messageMailer, budgetMailer };



