let projectFolder = 'https://s3-sa-east-1.amazonaws.com/marcell-assets/projects';

let p36 = [`${projectFolder}/p36-hours-01.png`, `${projectFolder}/p36-hours-02.png`, 
                 `${projectFolder}/p36-hours-03.png`],
    pomodoroNW = [`${projectFolder}/pomodoro-nw-01.png`, `${projectFolder}/pomodoro-nw-02.png`, 
      `${projectFolder}/pomodoro-nw-03.png`],
    hardost = [`${projectFolder}/safe-action-01.png`, `${projectFolder}/safe-action-02.png`, 
              `${projectFolder}/safe-action-03.png`],
    simpleCommander = [`${projectFolder}/simple-commander-01.png`];

let p36Images = [];
p36.forEach((link) => {
  p36Images.push({
    original: link, 
    thumbnail: link 
  });
});

let pomodoroNWImages = [];
pomodoroNW.forEach((link) => {
  pomodoroNWImages.push({
    original: link, 
    thumbnail: link
  });
});

let hardostImages = [];
hardost.forEach((link) => {
  hardostImages.push({
    original: link, 
    thumbnail: link
  });
});

let simpleCommanderImages = [];
simpleCommander.forEach((link) => {
  simpleCommanderImages.push({
    original: link, 
    thumbnail: link
  });
});

export default {
  'p36-hours': p36Images,
  'pomodoro-nw': pomodoroNWImages,
  'hardost': hardostImages,
  'simple-commander': simpleCommanderImages 
}



