import React, { Component} from 'react'; 
import BudgetForm from '../BudgetForm/BudgetForm';
import Footer from '../Footer/Footer';
import { Link } from 'react-router-dom';
import './Budget.css';

class Budget extends Component { 
  render(){
    return (
      <div>
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.subtitle}</h3>
          <Link to="access-budget">
            {this.props.lang.accessBudgetLink}
          </Link>
        </div>
        <div className="flex-row wrap inline-center g-pad">
          <div>
            <div className="info-panel">
              <h3>{this.props.lang.info.title}</h3>
              <p>{this.props.lang.info.message}</p>
            </div>
          </div>
          <BudgetForm lang={this.props.lang} />
        </div>
        <Footer 
            lang={this.props.footer}/>
      </div>
    );
  }
} 



export default Budget;



