import React, { Component} from 'react'; 
import './Home.css';
import ProjectsList from '../ProjectsList/ProjectsList';
import SkillsList from '../SkillsList/SkillsList';
import MessageForm from '../MessageForm/MessageForm';
import Button from '../Button/Button';
import Footer from '../Footer/Footer';
import { Link } from 'react-router-dom';

class Home extends Component { 
  render(){
    return (
      <div>
        <div className="flex-row inline-center g-pad">
          <div className="logo">
            <h1>./mmc</h1>
            <h3>Marcell Monteiro Cruz</h3>
            <h3>Software Developer</h3>
            <Link to="/budget">
              <Button styleName="primary main-button" 
                      text={this.props.lang.logo.button} />
            </Link>
            <a href='#message'>{this.props.lang.logo.link}</a>
          </div>
        </div>
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.projects.subtitle}</h3>
          <a target="_blank"
             rel="noopener noreferrer" 
             href="https://github.com/0000marcell">
            <i className="icon-github"></i>
            github
          </a>
        </div>
        <ProjectsList projects={this.props.lang.projects} />
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.skills.subtitle}</h3>
          <Link to="/skills"><i className="icon-attach"></i>{this.props.lang.skills.subtitle}</Link>
        </div>
        <SkillsList skills={this.props.lang.skills.data} />
        <p>{this.props.lang.skills.text} 
        <Link to="/skills">
          {this.props.lang.skills.linkName}
        </Link>
        </p>
        <MessageForm lang={this.props.lang.mailer}/>
        <Footer 
            lang={this.props.footer}/>
      </div>
    );
  }
} 



export default Home;



