import React, { Component} from 'react'; 
import brazilFlag from '../../images/brazil-flag.png';
import usaFlag from '../../images/usa-flag.png';
import './LangSelect.css';

class LangSelect extends Component { 

  constructor(props){
    super(props);
    this.state = this.getLangStateObj(props.lang);
    this.changeLang = this.changeLang.bind(this);
  }

  getLangStateObj(lang){
    return {
      pt: (lang === 'pt') ? 'lang-select-active' : '',
      en: (lang === 'en') ? 'lang-select-active' : '' 
    };
  }

  changeLang(lang){
    this.props.changeLang(lang);
    this.setState(this.getLangStateObj(lang));
  }
  
  render(){
    return (
      <div>
        <div className={`lang-select-button ${this.state.pt}`}
          onClick={() => { this.changeLang('pt') } }>
          <img src={brazilFlag} alt="brazil"/>
          <p>PT</p>
        </div>
        <div className={`lang-select-button ${this.state.en}`} 
          onClick={() => { this.changeLang('en') } }>
          <img src={usaFlag} alt="usa"/>
          <p>EN</p>
        </div>
      </div>
    );
  }
} 

export default LangSelect;



