import React, { Component} from 'react'; 
import Select from '../Select/Select';
import { select } from 'd3-selection';
import { extent } from 'd3-array';
import { scaleLinear, scaleBand } from 'd3-scale';
import { axisTop } from 'd3-axis';
import './SkillChart.css';

function styleAxis(axisNodes){
  let domain = axisNodes.selectAll('.domain');
    domain.attr('fill', 'none')
          .attr('stroke-width', 1)
          .attr('stroke', 'black');
    let ticks = axisNodes.selectAll('.tick line');
    ticks.attr('fill', 'none')
         .attr('stroke-width', 1)
         .attr('stroke', 'black');
}

class SkillChart extends Component { 
  constructor(props){
    super(props)
    this.updateBarChart = this.updateBarChart.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.createAxis = this.createAxis.bind(this);
    this.setupGraph = this.setupGraph.bind(this);
    this.state = {
      skillGroup: '',
      skillGroupOptions: [],
      maxItemNumber: 0
    }
    
  }

  componentDidMount() {
    let data = this.props.data;
    this.groupedSkills = data.skills.reduce((acc, item, i) => {
      if(!acc[item.group])
        acc[item.group] = []
      item.name = item.name.slice(0, 15);
      acc[item.group].push(item);
      return acc;
    }, {});
    let skillGroupOptions = Object.keys(this.groupedSkills);
    let maxItemNumber = 
          Math.max(...skillGroupOptions.map((key) => {
            this.groupedSkills[key] = 
              this.groupedSkills[key].map((value, i) => {
                value['id'] = i;
                return value;
              });
            return this.groupedSkills[key].length
          }));
    this.setupGraph(maxItemNumber);
    this.setState({skillGroupOptions: skillGroupOptions,
                   skillGroup: skillGroupOptions[0],
                   maxItemNumber: maxItemNumber});
  }

  setupGraph(maxItemNumber){
    let graph = {
      labelArea: 140,
      width: 232,
      barHeight: 20,
      lCol: "study",
      rCol: "practice",
    };
    graph.height = graph.barHeight * maxItemNumber + 100;
    graph.rightOffset = graph.width + graph.labelArea;
    graph.xFrom = scaleLinear()
            .range([0, graph.width]);
    graph.xTo = scaleLinear()
            .range([0, graph.width]);
    graph.y = scaleBand()
            .range([40, graph.height]);
    graph.y.domain([...Array(maxItemNumber).keys()]
      .map((i) => {
        return i;
      }));
    this.graph = graph;
    this.createAxis();
  }

  componentDidUpdate(){
    if(this.state.skillGroup)
      this.updateBarChart(this.groupedSkills[this.state.skillGroup]);
  }

  createAxis(){
    select(this.node).append('g')
                     .attr("class", "left-axis")
                     .attr("transform", "translate(0, 20)")
    select(this.node).append('g')
                     .attr("class", "right-axis")
                     .attr("transform", 
                    `translate(${this.graph.width + 
                        this.graph.labelArea}, 20)`)
  }

  handleChange(event){
    const target = event.target;
    this.setState({[target.name]: target.value});
  }

  updateBarChart(data) {
    const graph = this.graph;
    const rangeBand = 15;
    let chart = select(this.node)
                  .attr('class', 'chart')
                  .attr('width', graph.labelArea + graph.width + 
                    graph.width + 100)
                  .attr('height', graph.height);
    graph.xFrom.domain(extent(data, (d) => {
      return d[graph.lCol];
    })).nice();

    graph.xTo.domain(extent(data, (d) => {
      return d[graph.rCol];
    })).nice();

    let yPosByIndex = (d) => {
      return graph.y(d.id);
    };
    chart.selectAll("rect.left")
         .data(data)
         .attr("x", (d) => {
            return graph.width - graph.xFrom(d[graph.lCol]);
         })
         .attr("width", (d) => {
            return graph.xFrom(d[graph.lCol]);
         })
         .enter().append("rect")
         .attr("x", (d) => {
            return graph.width - graph.xFrom(d[graph.lCol]);
         })
         .attr("y", yPosByIndex)
         .attr("class", "left")
         .attr("width", (d) => {
            return graph.xFrom(d[graph.lCol]);
         })
         .attr("height", rangeBand);

    chart.selectAll("rect.left")
         .data(data)
         .exit().remove();

    chart.selectAll("text.name")
            .data(data)
            .text((d) => { return d.name; })
            .enter().append("text")
            .attr("x", (graph.labelArea / 2) + graph.width)
            .attr("y", (d) => {
                return graph.y(d.id) + rangeBand / 2;
            })
            .attr("dy", ".20em")
            .attr("text-anchor", "middle")
            .attr('class', 'name')
            .text((d) => { return d.name; });

    chart.selectAll("text.name")
         .data(data)
         .exit().remove();

    chart.selectAll("rect.right")
            .data(data)
            .attr("width", (d) => {
                return graph.xTo(d[graph.rCol]);
            })
            .enter().append("rect")
            .attr("x", graph.rightOffset)
            .attr("y", yPosByIndex)
            .attr("class", "right")
            .attr("width", (d) => {
              return graph.xTo(d[graph.rCol]);
            })
            .attr("height", rangeBand);

    chart.selectAll('rect.right')
         .data(data)
         .exit().remove();

    let leftAxis = axisTop(graph.xFrom.copy()
                        .domain(graph.xFrom.domain().reverse()));
    leftAxis.ticks(6, 's'); 
    let leftAxisNodes = select('.left-axis')
                        .call(leftAxis);
    //leftAxisNodes.call
    styleAxis(leftAxisNodes);    

    let rightAxis = axisTop(graph.xTo);
    rightAxis.ticks(6, 's');
    let rightAxisNodes = select('.right-axis') 
                    .call(rightAxis);
    styleAxis(rightAxisNodes);
  }

  render() {
    return (
      <div>
        <div className="flex-row inline-space-between graph-legend">
          <Select 
            name='skillGroup'
            value={this.state.skillGroup}
            options={this.state.skillGroupOptions}
            onChange={this.handleChange}
          />
          <div>
            <p>
              <span className="legend-block practice"></span>   
              {this.props.lang.legendPractice}
            </p>
            <p>
              <span className="legend-block study"></span>    
              {this.props.lang.legendStudy}
            </p>
          </div>
        </div>
        <div className="flex-row inline-center">
          <svg ref={node => this.node = node}>
          </svg>
        </div>
      </div>
    );
  }

} 

export default SkillChart;



