import React, { Component} from 'react'; 
import './CheckBox.css';

class CheckBox extends Component { 
  render(){
    return (
      <div className='checkbox'>
        <label>
          <input type="checkbox" {...this.props}
          />
          {this.props.text}
        </label>
      </div>
    );
  }
} 



export default CheckBox;



