import React, { Component} from 'react'; 
import './Select.css';

class Select extends Component { 
  render(){
    return (
      <select className='select' 
        {...this.props}>
        {this.props.options.map((option, i) => {
          return <option key={i} value={option}>{option}</option>
        })}
      </select>
    );
  }
} 



export default Select;



