import React, { Component} from 'react'; 
import SkillChart from '../SkillChart/SkillChart';
import RadarChart from '../RadarChart/RadarChart';
import Footer from '../Footer/Footer';
import { json } from 'd3-fetch';
import courses from './courses-books';
import data from '../../translations/en/data';
import './CV.css';
import { Link } from 'react-router-dom';

const dataSource = 'https://s3-sa-east-1.amazonaws.com/marcell-assets/skills2.json';

class CV extends Component { 
  constructor(props){
    super(props);
    this.state = {data: {}, response: 'pending'}
    json(dataSource).then((data) => {
      this.setState({data: data, response: 'completed'});
    }).catch(() => {
      this.setState({error: 
        'error trying to fetch the data, please try again! :(', 
        response: 'completed'})
    });
  }

  componentWillMount(){
    document.querySelector('body').style.minWidth = "700px"
  }

  componentWillUnmount(){
    document.querySelector('body').style.minWidth = ""
  }

  

  render(){
    if(this.state.response === 'pending'){
      return (
        <div className="cv-loading">
          <h3>{this.props.lang.loading}</h3>
        </div>
      );
    }

    if(this.state.error){
      return (
        <div className="cv-loading">
          <h3>{this.state.error}</h3>
        </div>
      );
    }

    return (
      <div>
        <div className="flex-row inline-center g-pad">
          <div className="cv-title">
            <h2>marcell monteiro cruz</h2>
            <h3>software developer</h3>
            <p>{this.props.lang.since}</p>
            <div className="cv-info g-pad">
              <h3><i className="icon-mail"></i>
                0000marcell@gmail.com</h3>
              <a target="_blank"
                 rel="noopener noreferrer"
                 href="https://0000marcell.github.io/">
                <h3><i className="icon-globe">
                  </i>{this.props.lang.website}</h3>
              </a>
              <a target="_blank" 
                 rel="noopener noreferrer"
                 href="https://github.com/0000marcell">
                <h3>
                  <i className="icon-github"></i>
                  github.com/0000marcell
                </h3>
              </a>
            </div>
          </div>
        </div>
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.skill}</h3>
          <Link to={this.props.lang.howIsCalculatedLink}>
            {this.props.lang.howIsCalculated}
          </Link>
        </div>
        <p>{this.props.lang.dataWarning}</p>
        <p>{this.props.lang.explanation} <Link to={this.props.lang.howIsCalculatedLink}>{this.props.lang.explanationLink}</Link></p>
        <SkillChart 
          data={this.state.data}
          lang={this.props.lang}/>
        <div className="flex-row subtitle g-pad">
          <h3>{this.props.lang.skillDistribution}</h3>
        </div>
        <RadarChart
          data={this.state.data}
          lang={this.props.lang}
        />
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.projects}</h3>
          <Link to="/">{this.props.lang.projectMoreInfo}</Link>
        </div>
        <ul>
          {data.projects.map((item, i) => {
            return (
              <li key={i}>
                <Link to={item.projectPageLink.href}>
                  {item.name}
                </Link>
              </li>
            )
          })
          }
        </ul>
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.courses}</h3>
          <a target="_blank"
           rel="noopener noreferrer"
           href="https://www.goodreads.com/user/show/72056073-marcell-cruz">
            goodreads
          </a>
        </div>
        <ul>
          {courses.map((item, i) => {
            return <li key={i}>{item}</li>
          })
          }
        </ul>
        
        <div className="flex-row inline-space-between subtitle g-pad">
          <h3>{this.props.lang.languagesTitle}</h3>
        </div>
        <ul>
        {
          this.props.lang.languages.map((item, i) => {
            return (
              <li key={i}>{item}</li>
            )
          })
        }
        </ul>
        <Footer lang={this.props.footer}/>
      </div>
    );
  }
} 



export default CV;



